@font-face {
  font-family: "special-font";
  src: url(../../public/font/TT\ Octosquares\ Trial\ Bold.ttf);
}

#root {
  padding: 5px;
  
  max-height: 100%;
  width: 100%;

  display: flex;
}

.bg {
  transition: background var(--transition-short-ms);
}

.bg[color-theme="light"] {
  background-image: url(../../public/images/backgrounds/light-mode.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.bg[color-theme="dark"] {
  background-image: url(../../public/images/backgrounds/dark-mode.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.bg-l-1 {
  background-image: var(--background-sunset-1);
  background-position: center;
  background-repeat: no-repeat;
}

.bg-d-1 {
  background-image: var(--background-dark-purple-light-blue-1);
  background-position: center;
  background-repeat: no-repeat;
}

.t-blue {
  color: rgb(40, 120, 250);
}

.s-font {
  font-family: "special-font";
  letter-spacing: 2px;
}
